<template>
  <v-container fluid>
    <v-layout row hidden-sm-and-up>
      <v-flex>
        <v-card elevation="0" fluid>
          <v-card-text>
            <div class="panal">
              <v-divider></v-divider>
              <h3 class="headline mb-0 text-xs-left">Personal Information</h3>
              <v-divider></v-divider>
            </div>
          </v-card-text>
          <v-card-title primary-title>
            <v-icon class='icon' title="day of birth">mdi mdi-cake-variant</v-icon>
            <div>{{dob}}</div>
          </v-card-title>
          <v-card-title primary-title>
            <v-icon class='icon' title="gender">mdi mdi-gender-male-female</v-icon>
            <div>Male</div>
          </v-card-title>
          <v-card-title primary-title>
            <v-icon class='icon' title="phone">mdi mdi-phone</v-icon>
            <div>{{phone}}</div>
          </v-card-title>
          <v-card-title primary-title>
            <v-icon class='icon' title="address">mdi mdi-map-marker</v-icon>
            <div>{{addr}}</div>
          </v-card-title>
          <v-card-title primary-title>
            <v-icon class='icon' title="email">mdi mdi-email-outline</v-icon>
            <div>{{email}}</div>
          </v-card-title>
          <v-card-title primary-title>
            <v-icon class='icon' title="github">mdi mdi-github-circle</v-icon>
            <a v-bind:href="github">{{github}}</a>
            <!-- <div>{{github}}</div> -->
          </v-card-title>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex>
        <v-card elevation="0">
          <v-card-text>
            <div class="panal">
              <v-divider></v-divider>
              <h3 class="headline mb-0 text-xs-left">Education</h3>
              <v-divider></v-divider>
            </div>
          </v-card-text>
          <v-card-title v-for="edu in education" :key="edu.index" primary-title>
            <v-icon class="icon">mdi mdi-circle-medium</v-icon>
            <div class="text-xs-left">{{edu}}</div>
          </v-card-title>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex>
        <v-card elevation="0">
          <v-card-text>
            <div class="panal">
              <v-divider></v-divider>
              <h3 class="headline mb-0 text-xs-left">Working Experience</h3>
              <v-divider></v-divider>
            </div>
          </v-card-text>
          <div v-for="work in workingExp" :key="work.index">
            <v-card-title primary-title>
              <!-- <v-icon class="icon">mdi mdi-circle-medium</v-icon> -->
              <h3>From {{work.from}} - {{work.to}}: {{work.jobTitle}} at {{work.companyName}}</h3>
            </v-card-title>
            <v-card-title class="text-xs-left">
              <h4 style="padding-left: 20px">Job Description:</h4>
            </v-card-title>
            <v-card-title class="text-xs-left" style="padding-left: 40px" v-for="jobDes in work.jobDes" :key="jobDes">
              <ul>
                <li>
                  {{jobDes}}
                </li>
              </ul>
            </v-card-title>
            <v-card-title class="text-xs-left">
              <h4 style="padding-left: 20px">Key Achivements:</h4>
            </v-card-title>
            <v-card-title class="text-xs-left" style="padding-left: 40px" v-for="jobAchi in work.jobAchivements" :key="jobAchi">
              <ul>
                <li>
                  {{jobAchi}}
                </li>
              </ul>
            </v-card-title>
          </div>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex>
        <v-card elevation="0">
          <v-card-text>
            <div class="panal">
              <v-divider></v-divider>
              <h3 class="headline mb-0 text-xs-left">Skills</h3>
              <v-divider></v-divider>
            </div>
          </v-card-text>
          <v-card-title v-for="skill in skills" :key="skill.index" style="padding-left: 20px; padding-bottom: 0px">
            <v-layout row>
              <v-flex xs1 class="text-xs-left" style="align-self: center; margin-right: 5px"><span>{{skill.name}}</span></v-flex>
              <v-flex class="text-xs-left" style="align-self: center;"><v-rating v-model="skill.rate" half-increments readonly></v-rating></v-flex>
            </v-layout>
          </v-card-title>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
var faker = require('faker/locale/en_US');
export default {
  name: 'career-info',
  data: function () {
    return {
      name: '',
      jobTitle: '',
      dob: '',
      phone: '',
      addr: '',
      email: '',
      github: '',
      education: [],
      workingExp: [],
      skills: [],
      companyName: '',
    }
  },
  created () {
    this.name = faker.fake("{{name.lastName}}, {{name.firstName}} {{name.suffix}}")
    this.jobTitle = faker.name.jobTitle()
    this.birthday = faker.fake("{{date.past}}")

    let dob = faker.date.past(50, new Date("Sat Sep 20 1992 21:35:02 GMT+0200 (CEST)"));
    this.dob = dob.getFullYear() + "-" + (dob.getMonth()+1) + "-" + dob.getDate();  // First month is "1"

    this.phone = faker.phone.phoneNumber()
    this.addr = faker.fake("{{address.streetAddress}}, {{address.state}}, {{address.city}}")
    this.email = faker.internet.email()
    this.github = faker.internet.url()
    this.education.push(faker.lorem.sentence())
    this.education.push(faker.lorem.sentence())
    this.education.push(faker.lorem.sentence())
    this.education.push(faker.lorem.sentence())
    this.education.push(faker.lorem.sentence())
    this.companyName = faker.company.companyName()

    this.workingExp.push({
      from: dob.getFullYear(),
      to: dob.getFullYear() + 3,
      jobTitle: faker.name.jobTitle(),
      companyName: faker.company.companyName(),
      jobDes: [
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence()
      ],
      jobAchivements: [
        faker.lorem.sentence(),
        faker.lorem.sentence()
      ]
    })

    this.workingExp.push({
      from: dob.getFullYear(),
      to: dob.getFullYear() + 3,
      jobTitle: faker.name.jobTitle(),
      companyName: faker.company.companyName(),
      jobDes: [
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence()
      ],
      jobAchivements: [
        faker.lorem.sentence(),
        faker.lorem.sentence()
      ]
    })

    this.workingExp.push({
      from: dob.getFullYear(),
      to: dob.getFullYear() + 3,
      jobTitle: faker.name.jobTitle(),
      companyName: faker.company.companyName(),
      jobDes: [
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence(),
        faker.lorem.sentence()
      ],
      jobAchivements: [
        faker.lorem.sentence(),
        faker.lorem.sentence()
      ]
    })
    this.skills.push({
      name: faker.lorem.word(),
      rate: Math.random() * 5
    })
    this.skills.push({
      name: faker.lorem.word(),
      rate: Math.random() * 5
    })
    this.skills.push({
      name: faker.lorem.word(),
      rate: Math.random() * 5
    })
    this.skills.push({
      name: faker.lorem.word(),
      rate: 2
    })
    this.skills.push({
      name: faker.lorem.word(),
      rate: 2.5
    })
  }
}
</script>

<style scoped>
.container {
  padding: 0px;
}
.v-card__text {
  padding-left: 2px;
  padding-right: 0px
}
.v-card__title {
  padding-top: 0px;
  padding-left: 6px;
  padding-bottom: 6px;
}
.icon {
  margin-right: 5px;
}
a:link {
  color: black;
  text-decoration: none;
}
.panal {
  background-color: #3d91cd;
}
</style>
